/* src/styles/AdminLogin.css */

/*    (   Flexbox) */
.login-page-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f0f2f5; /*    */
  background-image: linear-gradient(135deg, #f0f2f5 0%, #eef2f8 100%);
}

/*    */
.login-card {
  width: 100%;
  max-width: 420px;
  background: #ffffff;
  padding: 48px 40px;
  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.05); /*    */
  text-align: center;
}

/*   */
.login-header {
  margin-bottom: 32px;
}

.company-name {
  font-size: 0.85rem;
  color: #6c757d;
  letter-spacing: 1px;
  font-weight: 600;
  margin-bottom: 8px;
  text-transform: uppercase;
}

.service-title {
  font-size: 2rem;
  font-weight: 800;
  color: #1a237e; /*     */
  margin: 0 0 8px 0;
  letter-spacing: -0.5px;
}

.service-title .highlight {
  color: #3949ab; /*      */
}

.login-subtitle {
  font-size: 0.95rem;
  color: #666;
  font-weight: 400;
  margin: 0;
}

/*   */
.login-form {
  text-align: left;
}

.input-group {
  margin-bottom: 20px;
}

.input-group label {
  display: block;
  margin-bottom: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  color: #343a40;
}

.input-group input {
  width: 100%;
  padding: 12px 14px;
  font-size: 1rem;
  border: 1px solid #e1e4e8;
  border-radius: 6px;
  box-sizing: border-box;
  background-color: #fafbfc;
  transition: all 0.2s ease;
}

/*     */
.input-group input:focus {
  outline: none;
  border-color: #1a237e;
  background-color: #fff;
  box-shadow: 0 0 0 3px rgba(26, 35, 126, 0.1);
}

.input-group input::placeholder {
  color: #adb5bd;
}

/*   */
.checkbox-group {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  gap: 8px;
}

.checkbox-group input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
  accent-color: #1a237e;
}

.checkbox-group label {
  font-size: 0.9rem;
  color: #555;
  cursor: pointer;
  user-select: none;
}

/*   */
.login-btn {
  width: 100%;
  padding: 14px;
  background-color: #1a237e;
  color: #fff;
  font-size: 1.05rem;
  font-weight: 700;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 4px 6px rgba(26, 35, 126, 0.2);
}

.login-btn:hover {
  background-color: #15186a;
  transform: translateY(-1px);
  box-shadow: 0 6px 8px rgba(26, 35, 126, 0.25);
}

.login-btn:active {
  transform: translateY(0);
}

/*   */
.login-footer {
  margin-top: 32px;
  border-top: 1px solid #eee;
  padding-top: 20px;
}

.login-footer p {
  font-size: 0.75rem;
  color: #999;
  margin: 0;
}